.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 85vh;
}

.token-container {
  border: solid 1px black;
  padding: 10px;
  width: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100px;
  margin-bottom: 15px;
}

@media (max-width: 600px) {
  .container {
    padding: 10px;
  }
}

@media (max-width: 400px) {
  .container {
    font-size: 16px;
  }
}
